<script>
  import IcalExpander from "ical-expander";
  import Event from "./Event.svelte";

  async function fetchCalendar() {
    const res = await fetch("https://wdsa-calendar-proxy.vercel.app/api/calendar");
    const text = await res.text();
    if (res.ok) {
      return text;
    } else {
      throw new Error(text);
    }
  }

  function daysFromNow(n) {
    var today = new Date();
    var nextweek = new Date(
      today.getFullYear(),
      today.getMonth(),
      today.getDate() + n
    );
    return nextweek;
  }

  async function loadCalendar() {
    const ics = await fetchCalendar();
    const icale = new IcalExpander({ ics, maxIterations: 100 });
    const events = icale.between(daysFromNow(0), daysFromNow(14));

    const mappedEvents = events.events.map((e) => ({
      startDate: e.startDate,
      endDate: e.endDate,
      summary: e.summary,
      description: e.description,
    }));
    const mappedOccurrences = events.occurrences.map((o) => ({
      startDate: o.startDate,
      endDate: o.endDate,
      summary: o.item.summary,
      description: o.item.description,
    }));
    const allEvents = [].concat(mappedEvents, mappedOccurrences);
    console.debug(events);
    return allEvents.sort(
      (a, b) => new Date(a.startDate) - new Date(b.startDate)
    );
  }
  let promise = loadCalendar();
</script>

<div id="upcoming-events" class="lh-copy">
  <h2 class="f2 mt0 mb3">Upcoming Events</h2>
  {#await promise}
    <p>Loading...</p>
  {:then events}
    {#each events as event}
      <Event {event} />
    {/each}
  {:catch error}
    <p>Error: {error.message}</p>
  {/await}
</div>
